import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "./Home.css"; //  import

function Home() {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [userId, setUserId] = useState(null);
  const [userName, setUserName] = useState("");

  useEffect(() => {
    axios.get("/api/home", { withCredentials: true })
      .then(response => {
        setUserId(response.data.id);
        setUserName(response.data.name);
        setLoading(false);
      })
      .catch(error => {
        navigate("/");
      });
  }, [navigate]);

  const handleLogout = () => {
    axios.post("/api/logout", {}, { withCredentials: true })
      .then(() => {
        alert(" .");
        navigate("/");
      })
      .catch(() => {
        alert(" ");
      });
  };

  if (loading) {
    return <div> ...</div>;
  }

  return (
    <div className="home-container">
      {/*      */}
      <button className="logout-button" onClick={handleLogout}>
        
      </button>

      <p className="welcome-message">.</p>
      <p> : <strong>{userId}</strong></p>
      <p> : <strong>{userName}</strong></p>

      {/*    */}

      <div className="button-group">
        <button className="sky-button" onClick={() => navigate("/schedulePage")}>
          
        </button>
        <button className="sky-button" onClick={() => navigate("/notice")}>
          
        </button>
      </div>
    </div>
  );
}

export default Home;
